<template>
  <div>
    <br />

    <div class="flex">
      <div class="col">
        <mu-checkbox class="radio-apoyo" v-model="pagoAutomotrizActivo" label="Automotriz"></mu-checkbox>
        <br />
        <mu-text-field
          v-if="pagoAutomotrizActivo"
          v-model="pagosAutomotriz"
          label="Monto"
          prefix="$"
          label-float
        ></mu-text-field>
      </div>
      <div class="col">
        <mu-checkbox class="radio-apoyo" v-model="pagoHipotecarioActivo" label="Hipotecario"></mu-checkbox>
        <br />

        <mu-text-field
          v-if="pagoHipotecarioActivo"
          v-model="pagosHipotecario"
          label="Monto"
          prefix="$"
          label-float
        ></mu-text-field>
      </div>
    </div>
  </div>
</template>
<script>
import numeral from "numeral";

import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState(["pagosDeBuro", "pagosDeBuroActivos"]),
    pagoAutomotrizActivo: {
      get() {
        return this.pagosDeBuroActivos.pagosAutomotriz;
      },
      set(value) {
        let pagosDeBuroActivos = this.pagosDeBuroActivos;
        pagosDeBuroActivos.pagosAutomotriz = value;
        this.updatePagosDeBuroActivos(pagosDeBuroActivos);
      }
    },
    pagoHipotecarioActivo: {
      get() {
        return this.pagosDeBuroActivos.pagosHipotecario;
      },
      set(value) {
        let pagosDeBuroActivos = this.pagosDeBuroActivos;
        pagosDeBuroActivos.pagosHipotecario = value;
        this.updatePagosDeBuroActivos(pagosDeBuroActivos);
      }
    },
    pagosAutomotriz: {
      get() {
        return numeral(this.pagosDeBuro.pagosAutomotriz).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        let pagosDeBuro = this.pagosDeBuro;
        pagosDeBuro.pagosAutomotriz = valorNumerico;
        this.updatePagosDeBuro(pagosDeBuro);
      }
    },
    pagosHipotecario: {
      get() {
        return numeral(this.pagosDeBuro.pagosHipotecario).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        let pagosDeBuro = this.pagosDeBuro;
        pagosDeBuro.pagosHipotecario = valorNumerico;
        this.updatePagosDeBuro(pagosDeBuro);
      }
    }
  },
  methods: {
    ...mapMutations(["updatePagosDeBuro", "updatePagosDeBuroActivos"])
  }
};
</script>