
<template>
  <div class="padding-internal-card">
    <div>
      <br />
      <span class="blue-text cursor-pointer" @click="show = !show ">{{label}}</span>
      <br />
      <div v-if="show">
        <div class="center">
          <mu-radio value="1" v-model="_tipoDeBuro" label="Pagos Totales" class="radio-apoyo"></mu-radio>
          <mu-radio value="2" v-model="_tipoDeBuro" label="Pagos Mínimos" class="radio-apoyo"></mu-radio>
        </div>
        <!-- {{tipoDeBuro}} -->
        <div v-if="_tipoDeBuro == 2">
          <PagoMinimo />
        </div>
        <div v-if="_tipoDeBuro != 0">
          <Creditos />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import PagoMinimo from "./gastosMensuales/PagoMinimo";
import Creditos from "./gastosMensuales/Creditos";
import numeral from "numeral";

export default {
  components: { PagoMinimo, Creditos },
  data() {
    return {
      show: false
    };
  },
  computed: {
    ...mapState(["tipoDeBuro"]),
    _gastosMensuales: {
      get() {
        return numeral(this.gastosMensuales).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        this.updateGastosMensuales(valorNumerico);
      }
    },
    label() {
      if (this.show) {
        return "No incluír gastos en buró";
      } else {
        return "Incluír gastos en buró";
      }
    },
    _tipoDeBuro: {
      get() {
        return this.tipoDeBuro;
      },
      set(value) {
        this.updateTipoDeBuro(value);
      }
    }
  },
  methods: {
    ...mapMutations(["updateGastosMensuales", "updateTipoDeBuro"])
  },
  mounted() {
    if (this.$route.params.tipoDeBuro &&  this.$route.params.tipoDeBuro != 0) {
      this.show = true;
    }
  }
};
</script>
