<template>
  <div>
    <mu-text-field v-model="pagosMinimos" label="¿Cuál es tu pago mínimo?" prefix="$" label-float></mu-text-field>
  </div>
</template>
<script>
import numeral from "numeral";
import { mapState, mapMutations } from "vuex";

export default {
  computed: {
    ...mapState(["pagosDeBuro"]),
    pagosMinimos: {
      get() {
        return numeral(this.pagosDeBuro.pagosMinimos).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        let pagosDeBuro = this.pagosDeBuro;
        pagosDeBuro.pagosMinimos = valorNumerico;
        this.updatePagosDeBuro(pagosDeBuro);
      }
    }
  },
  methods: {
    ...mapMutations(["updatePagosDeBuro"])
  }
};
</script>