<template>
  <div>
    <Header />
    <Body />
    <ShareModal />
    <InfoModal />
    <!-- <ShareAll /> -->
    <br />
  </div>
</template>
<script>
import Header from "./components/header/Header";
import Body from "./components/body/Body";
import ShareModal from "./components/modals/ShareModal";
import InfoModal from "./components/modals/InfoModal";
// import ShareAll from "./components/modals/ShareAll";
import { mapMutations } from "vuex";
export default {
  components: {
    Header,
    Body,
    ShareModal,
    InfoModal
    // ShareAll
  },
  methods: {
    ...mapMutations([
      "updateName",
      "updateEmail",
      "updatePhone",
      "updateTipoDeIngresoSeleccionado",
      "updateTipoDeApoyoSeleccionado",
      "updateMontoDeApoyo",
      "updateIngresoMensual",
      "updateValorDelInmueble",
      "updateTipoDePago",
      "updatePlazoSeleccionado",
      "updateMontoDeCredito",
      "updateTipoDeBuro",
      "updatePagosDeBuro",
      "updatePagosDeBuroActivos"
    ]),
    verifyURLShare() {
      var params = this.$route.params;
      console.log(params);

      if (params.share) {
        var name = params.name.replace(/_/g, " ");
        var tipoDeIngreso = params.tipoDeIngreso.replace(/_/g, " ");
        this.updateName(name);
        this.updateEmail(params.email);
        this.updatePhone(params.phone);
        this.updateTipoDeIngresoSeleccionado(tipoDeIngreso);
        this.updateTipoDeApoyoSeleccionado(params.tipoDeApoyo);
        this.updateMontoDeApoyo(parseFloat(params.montoDeApoyo));
        this.updateIngresoMensual(parseFloat(params.ingresoMensual));
        this.updateValorDelInmueble(parseFloat(params.valorDelInmueble));
        this.updateTipoDePago(params.tipoDePago);
        this.updatePlazoSeleccionado(params.plazo);
        this.updateMontoDeCredito(
          parseFloat(params.montoDeCreditoPorMoficicacion)
        );
        this.updateTipoDeBuro(params.tipoDeBuro);
        this.updatePagosDeBuro({
          pagosMinimos: params.pagosMinimos,
          pagosAutomotriz: params.pagosAutomotriz,
          pagosHipotecario: params.pagosHipotecario
        });
        this.updatePagosDeBuroActivos({
          pagosAutomotriz:
            params.APagosAutomotriz &&
            (params.APagosAutomotriz === "true" ? true : false),
          pagosHipotecario:
            params.APagosAutomotriz &&
            (params.APagosHipotecario === "true" ? true : false)
        });
      }
    }
  },
  mounted() {
    let data = {};
    this.verifyURLShare();
  }
};
</script>
